function Card(props) {
  return (
    <div className="card-layout">
      <div className="card-attribute">{props.cardAttribute}</div>
      <div className="card-value">{props.cardValue}</div>
      {props.cardAttribute === "Background" &&
        background.map(
          (item, index) =>
            item.trait === props.cardValue && (
              <div key={index} className="card-value">
                {item.occurrence}
              </div>
            )
        )}
      {props.cardAttribute === "Headgear" &&
        headgear.map(
          (item, index) =>
            item.trait === props.cardValue && (
              <div key={index} className="card-value">
                {item.occurrence}
              </div>
            )
        )}
      {props.cardAttribute === "Nose" &&
        nose.map(
          (item, index) =>
            item.trait === props.cardValue && (
              <div key={index} className="card-value">
                {item.occurrence}
              </div>
            )
        )}
      {props.cardAttribute === "Back Accessory" &&
        back.map(
          (item, index) =>
            item.trait === props.cardValue && (
              <div key={index} className="card-value">
                {item.occurrence}
              </div>
            )
        )}
      {props.cardAttribute === "Eyes" &&
        eyes.map(
          (item, index) =>
            item.trait === props.cardValue && (
              <div key={index} className="card-value">
                {item.occurrence}
              </div>
            )
        )}
      {props.cardAttribute === "Body" &&
        body.map(
          (item, index) =>
            item.trait === props.cardValue && (
              <div key={index} className="card-value">
                {item.occurrence}
              </div>
            )
        )}
      {props.cardAttribute === "Mouth" &&
        mouth.map(
          (item, index) =>
            item.trait === props.cardValue && (
              <div key={index} className="card-value">
                {item.occurrence}
              </div>
            )
        )}
    </div>
  );
}
export default Card;
