function Admin() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);

  const [owner, setOwner] = useState("");
  const [master, setMaster] = useState("");
  const [checkAdd, setCheckAdd] = useState("");
  const [addressmintedBala, setAddressmintedBala] = useState("");

  const [feedback, setFeedback] = useState("");
  const setBaseUrl = () => {
    blockchain.smartContract.methods
      .setBaseURI(
        "https://gateway.pinata.cloud/ipfs/QmaiXd6twHM6mPxN1NTh4dBWkM6ZwZVy43zFBimQzutxp1/"
      )
      .send({
        from: blockchain.account,
      });
  };
  const sendOwner = () => {
    blockchain.smartContract.methods.transferOwnership(owner).send({
      from: blockchain.account,
    });
  };
  const setMasterF = () => {
    blockchain.smartContract.methods.setMaster(master).send({
      from: blockchain.account,
    });
  };
  const checkWhite = async () => {
    const temp = await blockchain.smartContract.methods.isWhitelisted(checkAdd).call();
    console.log("tmp:", temp);
  };
  const getMintedBala = async () => {
    const temp = await blockchain.smartContract.methods.addressMintedBalance(checkAdd).call();
    console.log("tmp:", temp);
  }
  const claimNFTs = () => {
    setFeedback("Minting your Official BooCrew NFT...");
    setClaimingNft(true);
    blockchain.smartContract.methods
      .whitelistUsers(addresses)
      .send({
        from: blockchain.account,
      })
      .then((receipt) => {
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };
  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };
  useEffect(() => {
    getData();
  }, [blockchain]);
  return (
    <React.Fragment>
      <Header></Header>
      <div className="main-bg-color body-height-init text-white">
        <div className="d-flex justify-content-center">
          {blockchain.account === "" || blockchain.smartContract === null ? (
            <button
              className="init-button"
              onClick={(e) => {
                e.preventDefault();
                dispatch(connect());
                getData();
              }}
            >
              Connect
            </button>
          ) : (
            <Row>
              <Col md={6}>Minted Amount</Col>
              <Col md={6}>{data.totalSupply}</Col>
              <Col md={12}>
                <button
                  onClick={(e) => {
                    e.preventDefault();
                    claimNFTs();
                    getData();
                  }}
                >
                  Set whitelist
                </button>
              </Col>
              <Col md={12}>
                <button
                  onClick={(e) => {
                    e.preventDefault();
                    setBaseUrl();
                    getData();
                  }}
                >
                  Set BaseURL
                </button>
              </Col>
              <Col md={6}>
                <input
                  value={owner}
                  placeholder="Owner"
                  onChange={(e) => setOwner(e.target.value)}
                />
              </Col>
              <Col md={6}>
                <button
                  onClick={(e) => {
                    e.preventDefault();
                    sendOwner();
                    getData();
                  }}
                >
                  Send Owner
                </button>
              </Col>
              <Col md={6}>
                <input
                  value={master}
                  placeholder="Owner"
                  onChange={(e) => setMaster(e.target.value)}
                />
              </Col>
              <Col md={6}>
                <button
                  onClick={(e) => {
                    e.preventDefault();
                    setMasterF();
                    getData();
                  }}
                >
                  Send master
                </button>
              </Col>
              <Col md={6}>
                <input
                  value={checkAdd}
                  placeholder="Test"
                  onChange={(e) =>setCheckAdd(e.target.value)}
                />
              </Col>
              <Col md={6}>
                <button
                  onClick={(e) => {
                    e.preventDefault();
                    // checkWhite();
                    getMintedBala();
                    getData();
                  }}
                >
                  Send master
                </button>
              </Col>
            </Row>
          )}
        </div>
      </div>
    </React.Fragment>
  );
}

export default Admin;
